import React from "react";
import PropTypes from "prop-types";

import EmployeeListEntry from "./OraEmployeeListEntry";

export default class OraEmployeeList extends React.Component {
    constructor() {
        super();
    }

    radioChanged(changeEvent) {
        this.props.onSelectionChanged(changeEvent.target.value);
    }

    render() {
        return (
                <table className="opl-table opl-table-basic">
                    <thead>
                        <tr>
                            <th className="u-font-bold" style={{width: 10}}/>
                            <th className="u-font-bold">ID</th>
                            <th className="u-font-bold">Nazwisko</th>
                            <th className="u-font-bold">Imię</th>
                            <th className="u-font-bold">Login</th>
                            <th className="u-font-bold">Kod BSCS</th>
                            <th className="u-font-bold">Lokalizacja</th>
                            <th className="u-font-bold">Kanał</th>
                            <th className="u-font-bold">Kanał SIS</th>
                        </tr>
                    </thead>
                    <tbody>
                        {this.props.employees.map((e, i) => {
                            return <EmployeeListEntry employee={e} radioChanged={this.radioChanged.bind(this)}
                                                      key={this.props.listId + "-" + i}/>;
                        })}
                    </tbody>
                </table>
        );
    }
}

OraEmployeeList.propTypes = {
    listId: PropTypes.string.isRequired,
};

OraEmployeeList.defaultProps = {
    employees: [],
    listId: "default",
    onSelectionChanged: function(sisId) {},
};
